import React from 'react';
import '../homepage/homepage.css'
function Homepage() {
    return (

        <div className='body'>
            <section>
                {/* logo part */}
                <div className='logoarea'>
                    <img src={require('../asserts/logo.png')} className='logo' />
                </div>

                {/* awardimage */}
                <div className='row top'>
                    <div className='row leftSide'>

                        <img src={require('../asserts/1.png')} className='awardimg' />

                    </div>

                    {/* content side */}
                    <div className='row rightSide'>

                        <div className='content1'>
                            <p className='p'>C.R.I. PUMPS WINS THE NATIONAL ENERGY CONSERVATION AWARD 2018 for the 4th time.</p>
                            <ul>
                                <li> C.R.I.'s energy efficient products are well recognized by various Government Institutions, as trustworthy products for various projects across the globe to save energy.</li>
                                <li>
                                    C.R.I. is the highest contributor in the country for the projects of EESL (Energy Efficiency Services Limited) to replace the old inefficient pumps with 5 Star rated energy efficient smart pumps with IoT enabled control panel.
	                     </li>
                            </ul>
                            <div className='img2area'>
                                <img src={require('../asserts/2.png')} className='img2' />
                            </div>
                            <div className='para'>
                                <p>Government of India has awarded the <span>"National Energy Conservation Award 2018"</span>. Mr. G. Selvaraj, Joint Managing Director of C.R.I. Group received the award from Smt. Sumitra Mahajan, Speaker of Lok Sabha &#38; Shri. Raj Kumar Singh, Honorable Minister of State.</p>
                            </div>

                        </div>
                    </div>
                </div>
            </section>

            {/* center area of pump */}
            <div className='center'>
                <div className='center1'>
                    <span>INSTALLED OVER 10 LAKHS STAR RATED PUMPSETS ACROSS THE COUNTRY RESULTING IN A CUMULATIVE SAVING OF MORE THAN 9,000 MILLION UNITS OF POWER FOR THE NATION.</span>
                </div>
                <div className='img3area'>
                    <img src={require('../asserts/3.png')} className='img3' />
                </div>
                <div className='textarea'>
                    <p>Valves - Pumps - Pipes - IoT Drives &#38; Controllers - Wires &#38; Cables - Solar Systems - Motors </p>
                </div>
                <div className='redLine'>

                </div>
            </div>

            {/* Down part */}

            <div className='down'>
                <div className='cri'>
                    <p>C.R.I. FLUID SYSTEMS PRODUCTS CATER TO DIVERSE SEGMENTS</p>
                </div>
                <div className='provide'>
                    <span>CHEMICALS <span>|</span> PROCESS POWER WATER <span>|</span> WASTE WATER OILS <span>|</span> GAS PHARMA SUGARS <span>|</span> DISTILLERIES PAPER <span>|</span> PULP MARINE <span>|</span> DEFENCE METAL <span>|</span> MINING FOOD <span>|</span> BEVERAGE PETROCHEMICAL <span>|</span> REFINERIES SOLAR BUILDING HVAC FIRE FIGHTING AGRICULTURE <span>|</span> RESIDENTIAL</span>
                </div>
            </div>
            <div className='footer'>
                <div className='section'>
                    <div className='iconarea'>
                        <img src={require('../asserts/phone.png')} className='icon' />
                    </div>
                    <p>TollFree 1800 200 1234</p>
                </div>
                <div className='section'>
                <div className='iconarea'>
                        <img src={require('../asserts/facebook.png')} className='icon' />
                    </div>
                    <p>www.facebook.com/cripumps</p>
                </div>
                <div className='section'>
                <div className='iconarea'>
                        <img src={require('../asserts/internet.png')} className='icon' />
                    </div>
                    <p>crigroups.com</p>
                </div>

            </div>



        </div>
    );
}

export default Homepage;
